import React from 'react'
import logo from '../../Assets/logo.png'

const PageHome = () => {
  return (
    <div className='d-flex text-center ' style={{minHeight:"100vh"}}>
        <div className="px-4 py-5 my-5 text-center">
                <img className="d-block mx-auto mb-4" src={logo} alt="" width="102" height="87" style={{scale:"2"}}/>
                <h1 className="display-5 fw-bold">Egzaminy Zawodowe INF</h1>
                <div className="col-lg-6 mx-auto">
                <p className="lead mb-4">Przygotuj si do egzaminw zawodowych z informatyki dziki naszym zasobom edukacyjnym.
                   Oferujemy przykadowe zadania, szczegowe rozwizania oraz materiay pomocnicze, 
                   ktre pomog Ci osign sukces na egzaminie.</p>

                <div className="d-grid gap-2 d-sm-flex justify-content-sm-center">
                    <button type="button" className="btn btn-primary btn-lg px-4 gap-3">Egzaminy</button>
                    <button type="button" className="btn btn-outline-secondary btn-lg px-4">Kontakt</button>
                    <button type="button" className="btn btn-info btn-lg px-4 text-light">Materiay</button>
                </div>
                </div>
            </div>
    </div>
  )
}

export default PageHome