import React from 'react'
import egz1 from '../../Assets/egz1.jpg'
import egz2 from '../../Assets/egz2.jpg'

const PageEgzaminy = () => {
  return (
    <div className='d-flex justify-content-center text-center' style={{ minHeight: "100vh" }}>
            <div className="container d-flex justify-content-center align-items-center px-4 py-5  h-100">
                <div className="row w-100">
                    <div className="col-sm-6 mb-4">
                        <div className="card h-100">
                            <img className="card-img-top " src={egz1} style={{ maxHeight: "250px", objectFit: "cover" }}  alt="Egzamin INF03" />
                            <div className="card-body">
                                <h5 className="card-title">Egzamin E14/EE09/INF03</h5>
                                <p className="card-text">Sprawd rozwizania do egzaminu INF03, ktry obejmuje projektowanie stron,obrbka grafiki lub dwieku oraz tworzenie i zarzdzanie bazami danych.
                                    Na tym egzaminie gwnie posugujesz si sql,html,css,php.
                                </p>
                                <a href="/EgzaminyZawodoweINF/#/egzaminy/inf03" className="btn btn-primary">Sprawd Rozwizania arkuszy</a>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6 mb-4">
                        <div className="card h-100">
                            <img className="card-img-top" src={egz2} alt="Egzamin INF04" style={{ maxHeight: "250px", objectFit: "cover" }} />
                            <div className="card-body">
                                <h5 className="card-title">Egzamin INF04</h5>
                                <p className="card-text">Sprawd rozwizania do egzaminu INF04, koncentrujcego si na programowaniu aplikacji i zarzdzaniu systemami operacyjnymi.
                                    Na tym arkuszu tworzysz gwnie aplikacje w React,Android Studio,WPF,Express oraz konsolowe.
                                </p>
                                <a href="#" className="btn btn-primary disabled">Sprawd Rozwizania arkuszy</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    </div>
  )
}

export default PageEgzaminy